<template>
<el-container style="width=200px">
    <el-container class="layout-container-demo" style="display:flex">
        <el-button style="margin-right:50px;margin-top:30px" @click="kick">踢出成员</el-button>
        <el-avatar-gorup >
            <el-avatar v-for="user in userList" :key="user.idx" size="large" fit="fit" :index="String(user)"
                @mouseover="changeCursor" @click="selectUser" style="margin-right:10px">
            {{ user }}
            </el-avatar>
        </el-avatar-gorup>
    </el-container>
</el-container>
</template>

<script>
import { socketManager,ws_username,setter } from '../assets/socket.js';
import { onMounted, ref } from 'vue';
import {useRouter} from "vue-router";
import { ElMessageBox } from 'element-plus';
import { ElAvatar, ElDropdown, ElDropdownItem, ElDropdownMenu, ElIcon, ElMenu, ElMenuItem, ElMenuItemGroup, ElScrollbar, ElRow, ElCol, ElButton } from 'element-plus'
import { Menu as IconMenu, Message, Setting } from '@element-plus/icons-vue'

let ws;
export default {
    setup() {
        const innerRef = ref(null);
        const router = useRouter();
        setter("ADMIN");
        console.log(ws_username);
        socketManager.connect();
        ws = ref(socketManager.getSocket()).value;
        
        console.log("------------------");
        console.log(ws);
        console.log("------------------");

        return {
            innerRef,ws
        };
    },
    name: "Admin",
    components: {
        ElAvatar,
        ElIcon,
        ElMenu,
        ElMenuItem,
        ElMenuItemGroup,
        ElScrollbar,
        IconMenu,
        Message,
        Setting,
        ElRow,
        ElCol,
        ElButton
    },
    data() {
        return {
            userList: [],
            target: null
        };
    },
    mounted() {
        const handlewsOpen = (e) => {
            console.log("websocket open 连接成功");
            let obj = {
                type:"update_user"
            };
            ws.send(JSON.stringify(obj));
        };

        const handlewsClose = (e) => {
            console.log("websocket close 关闭连接");
        };

        const handlewsError = (e) => {
            console.log("websocket error 连接错误", e);
        };

        const handlewsMessage = (e) => {
            let data = JSON.parse(e.data);
            console.log("websocket message 服务器消息", data);
            if(data.type == "update_user"){
                const content = data.content;
                if(content=="^") this.userList = [];
                else this.userList = data.content.split("^");
            }
        };
        ws.addEventListener("open", handlewsOpen, false);
        ws.addEventListener("close", handlewsClose, false);
        ws.addEventListener("error", handlewsError, false);
        ws.addEventListener("message", handlewsMessage, false);
        this.chatRoom = this.$refs.chatRoom;
        console.log("this.chatRoom", this.chatRoom);
    },
    methods: {
        changeCursor(){
            document.body.style.cursor = "pointer";
        },
        selectUser(e){
            const username = e.target.innerHTML;
            this.target = username;
            console.log(this.target);
        },
        kick(){
            const username = this.target;
            if(username==null){
                ElMessageBox.alert("未选择成员","错误");
                return;
            }
            console.log(username);
            let obj = {
                type:"exile",
                target:username
            };
            ws.send(JSON.stringify(obj));
            ElMessageBox.alert('已踢出该成员','成功');
        }
    }
};
</script>

<style scoped>
.layout-container-demo .el-header {
    position: relative;
    /* background-color: var(--el-color-primary-light-7); */
    color: var(--el-text-color-primary);
}

.layout-container-demo .el-aside {
    color: var(--el-text-color-primary);
    /* background: var(--el-color-primary-light-8); */
}

.layout-container-demo .el-menu {
    border-right: none;
}

.layout-container-demo .el-main {
    padding: 0;
}

.layout-container-demo .toolbar {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    right: 5px;
}

.chat-room .msg {
    background-color: #12B7F5;
    /* box-shadow: rgba(18, 23, 45, 0.6) 0px 8px 24px; */
    border-radius: 4px;
    padding: 10px;
    font-size: 18px;
    line-height: 16px;
    /* width: auto; */
    /* max-width: 330px; */
    margin-top: 20px;
    color: white;
}

.msg2 {
    background-color: #12B799;
    /* box-shadow: rgba(18, 23, 45, 0.6) 0px 8px 24px; */
    border-radius: 4px;
    padding: 10px;
    font-size: 18px;
    line-height: 16px;
    /* width: auto; */
    /* max-width: 330px; */
    margin-top: 20px;
    color: white;
}
</style>